.about {
padding: "120px 120px 120px 120px";
}
img.about__img{
    width: 100%;
    border-radius: 20px;
}
h1.about__heading{
    margin-bottom: 10px;
    font-weight: 700;
    text-align: start;
    font-size: 60px;
    font-family: Montserrat,sans-serif;
}
button.about.btn.pointer{
    width: 195px;
    height: 55px;
    font-size: 18px;
    text-transform: capitalize;
    transition: .4s ease;
}
.about__button{
    padding: 20px 0;
}

a.bottom__to__top {
    height: 40px;
    width: 40px;
    position: fixed;
    bottom: 4%;
    right: 2%;
    background-color: #f9004D;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    transition: .4s all ease;
    z-index: 1000;
}
a.bottom__to__top.active{
    display: flex;
}

@media only screen and (max-width: 1024px) {
    .p__color{
        font-size: 15px;
    }    
}
