.contact{
    background-color: #191919;
}
.input__box{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
h1.hire__text{
    font-size: 60px;
}
p.hire__text{
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    color: rgba(29, 29, 36, .75);
}
img.contact__img{
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 10px;
}
input.contact{
   display: block;
    width: 100%;
    padding: 0 20px;
    border-radius: 1px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    height: 50px;
    line-height: 46px;
    margin: 20px 0px; 
    outline: none;
    color: #fff;
    font-size: 15px;
    letter-spacing: 1px;
    background: none;
    border: 2px solid hsla(0, 0%, 100%, .2);
}
textarea.message{
    height: 120px;
    background: none;
    padding: 0 20px;
    width: 100%;
    color: #fff;
    font-size: 15px;
    letter-spacing: .1px;
    border: 2px solid hsla(0, 0%, 100%, .2);
}
button.btn.contact.pointer{
    margin: 20px 0;
    align-self: center;
}