h1.heading{
    font-size: 60px;
    text-align: center;
    font-weight: 700;
    margin-bottom: 10px;
    font-family: poppins, sans-serif;
}
p.heading.p__color {
    margin-bottom: 0;
    text-align: center;
    line-height: 1.2;
}
.project__box{
    border-radius: 10px;
    width: 100%;
    background-color: #191919;
    padding: 30px;
    transition: .4s ease;
}
.project__box:hover{
    transform: 5px;
    background-image: linear-gradient(90deg,#f61b10,#ef0963);
}
.space{
    padding: 0px 0px 30px;
}
p.project__text{
    font-size: 19px;
    font-family: poppins,sans-serif;
    padding: 10px;
}